<template>
  <div class="items-page">
    <!-- header -->
    <v-toolbar dense color="primary" :dark="true">
      <v-icon class="mr-4" @click="backToList">mdi-arrow-left</v-icon>

      <v-toolbar-title>
        <p class="text large">Random Picker</p>
        <p class="text small">Select / Edit List Items</p>
      </v-toolbar-title>

      <v-spacer></v-spacer>
    </v-toolbar>

    <!-- list of random picker packs -->
    <v-main>
      <center>
        <v-card>
          <v-row class="py-1 px-4 align-center">
            <v-col class="pa-0 mr-3 text-center">
              <p class="text grey--text">List Name:</p>
            </v-col>
            <p class="text pink--text text-body-1">{{ listData.text }}</p>

            <v-spacer></v-spacer>

            <v-icon class="mx-2" color="grey" size="20px" @click="showEditListNameDialog">mdi-pencil</v-icon>
          </v-row>
        </v-card>

        <p
          v-if="!listData.options.length"
          class="text small grey--text text--darken-2 ma-4"
        >Press the + button to add new item to the list.</p>
      </center>

      <v-list flat dense>
        <v-list-item-group color="pink">
          <template v-for="(option, index) in listData.options">
            <v-list-item :key="'list-option_' + index" @click="showAddDialog(option)">
              <!-- option color -->
              <color-picker :color="option.color" />

              <!-- option name -->
              <v-list-item-content>
                <v-list-item-title class v-text="option.text"></v-list-item-title>
              </v-list-item-content>

              <v-spacer />

              <!-- delete icon -->
              <v-list-item-icon @click="showDeleteDialog($event, option)">
                <v-icon class="mx-2" color="grey" size="20px">mdi-delete-forever</v-icon>
              </v-list-item-icon>
            </v-list-item>

            <!-- items devider -->
            <v-divider v-if="index + 1 < listData.options.length" :key="index"></v-divider>
          </template>
        </v-list-item-group>
      </v-list>

      <!-- edit list name dialog -->
      <edit-name
        v-if="editNameDialog.show"
        :dialog="editNameDialog"
        :editMode="true"
        @save="editListName"
      />

      <!-- add new option dialog -->
      <edit-option
        v-if="addState.show"
        :dialog="addState"
        @setColor="selectColor"
        @save="updateOptions"
      />

      <!-- delete option dialog -->
      <delete-dialog
        v-if="deleteState.show"
        :dialog="deleteState"
        text="Are you sure you want to delete the item:"
        @delete="deleteOption"
      />

      <v-fab-transition>
        <v-btn
          v-if="!addState.show"
          @click="showAddDialog()"
          fixed
          fab
          dark
          small
          bottom
          right
          color="pink"
        >
          <v-icon>mdi-plus</v-icon>
        </v-btn>
      </v-fab-transition>
    </v-main>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Items",

  props: ["id"],

  components: {
    EditName: () => import("@/components/dialogs/EditName"),
    DeleteDialog: () => import("@/components/dialogs/DeleteDialog"),
    EditOption: () => import("@/components/dialogs/EditOption"),
    ColorPicker: () => import("@/components/ColorPicker")
  },

  data: () => ({
    editNameDialog: {
      show: false,
      item: null
    },

    deleteState: {
      show: false,
      item: null
    },

    newOption: { text: "New Item", color: "", id: null },

    addState: {
      show: false,
      item: null
    }
  }),

  methods: {
    showEditListNameDialog() {
      this.editNameDialog.show = true;
      this.editNameDialog.item = Object.assign({}, this.listData);
    },

    editListName() {
      this.editNameDialog.show = false;
      this.$store.commit("updateList", this.editNameDialog.item);
    },

    selectColor(color) {
      this.addState.item.color = color;
    },

    showAddDialog(option = { ...this.newOption }) {
      this.addState.show = true;
      if (!option.color) {
        option.colorIndex = this.listData.options.length;
      }
      this.addState.item = option;
    },

    updateOptions() {
      this.addState.show = false;
      this.$store.commit("updateOptions", {
        item: this.listData,
        option: this.addState.item
      });
    },

    showDeleteDialog(event, option) {
      event.stopPropagation();
      this.deleteState.show = true;
      this.deleteState.item = option;
    },

    deleteOption() {
      this.deleteState.show = false;
      this.$store.commit("removeOption", {
        item: this.listData,
        option: this.deleteState.item
      });
    },

    backToList() {
      this.$router.push({ name: "List" });
    }
  },

  computed: {
    listData: function() {
      return this.list.find(listItem => listItem.id == this.id);
    },

    ...mapState(["list"])
  },

  created() {
    if (!this.$store.getters.listData(this.id)) {
      this.$router.replace({ name: "List" });
    }
  }
};
</script>
